.usersList {
   flex: 4;
}

.userListUser {
   display: flex;
   align-items: center;
}

.userListImg {
   width: 40px;
   height: 40px;
   object-fit: cover;
   border-radius: 50%;
   margin-right: 10px;
}

.edit {
   cursor: pointer;
   border: none;
   color: white;
   background-color: teal;
   margin-right: 10px;
   border-radius: 10px;
   padding: 5px;
}

.delete {
   cursor: pointer;
   color: red;
}
