.products {
   flex: 4;
}
.productListItem {
   display: flex;
   align-items: center;
}
.productListImg {
   width: 40px;
   height: 40px;
   object-fit: cover;
   border-radius: 50%;
   margin-right: 10px;
}

.productListedit {
   cursor: pointer;
   border: none;
   color: white;
   background-color: teal;
   margin-right: 10px;
   border-radius: 10px;
   padding: 5px;
}

.delete {
   cursor: pointer;
   color: red;
}
