.sideBar {
   flex: 1 1;
   background-color: rgb(251, 251, 255);
   /* Making it a 100vh will overflow.  Thus, calc will prevent it */
   height: calc(100vh - 50px);
   position: -webkit-sticky;
   position: sticky;
   top: 50px;
}

.sideBarWrapper {
   padding: 20px;
   color: #555;
}

.sideBarMenu {
   margin-bottom: 10px;
}

.sideBarTitle {
   font-size: 13px;
   color: rgb(187, 186, 186);
}
.sideBarList {
   list-style: none;
   padding: 5px;
}

.sideBarListItem {
   padding: 5px;
   cursor: pointer;
   display: flex;
   align-items: center;
   border-radius: 10px;
}
/* 'Home' initially has border radius color */
.sideBarListItem.active,
.sideBarListItem:hover {
   background-color: rgb(240, 240, 255);
}

.sideBarIcons {
   /* Changing the size for material icons '!important' */
   font-size: 20px !important;
   margin-right: 5px;
}

.topbar {
   width: 100%;
   background-color: white;
   height: 50px;
   /* scrolling down will always show on top and over what ever elements */
   position: -webkit-sticky;
   position: sticky;
   top: 0;
   z-index: 999;
}

.topbarWrapper {
   height: 100%;
   padding: 0px 20px;

   align-items: center;
   display: flex;
   justify-content: space-between;
}

.logo {
   font-weight: bold;
   font-size: 30px;
   cursor: pointer;
   color: darkblue;
   margin-right: 10px;
   /* Light gray color */
   color: #555;
}

.topBarIconsContainer {
   position: relative;
   cursor: pointer;
}

.topIconBadge {
   position: absolute;
   background-color: red;
   right: 0px;
   top: -5px;
   width: 15px;
   height: 15px;
   border-radius: 50%;
   display: flex;
   align-items: center;
   justify-content: center;
   font-size: 10px;
}
.topRight {
   display: flex;
   align-items: center;
}

.profilePicture {
   width: 40px;
   height: 40px;
   border-radius: 50%;
   object-fit: cover;
   cursor: pointer;
}

.container {
   display: flex;
   margin-top: 10px;
}

.chart {
   margin: 20px;
   padding: 20px;
   box-shadow: 5px 5px 15px -2px #000000;
}
.chartTitle {
   margin-bottom: 20px;
}

.featured {
   display: flex;
   justify-content: space-between;
   width: 100%;
}
.featuredItem {
   margin: 0px 20px;
   padding: 30px;
   border-radius: 10px;
   flex: 1 1;
   cursor: pointer;
   box-shadow: 5px 5px 15px -2px #000000;
}
.featuredTitle {
   font-size: 20px;
}
.featuredMoneyContainer {
   margin: 10px 0px;
   display: flex;
   align-items: center;
}
.featuredMoney {
   font-weight: 600;
   font-size: 30px;
}
.featuredMoneyRate {
   display: flex;
   align-items: center;
   margin-left: 10px;
}

.featuredIcon {
   margin-left: 5px;
   font-size: 14px;
   color: green;
}
.featuredIcon.negative {
   color: red;
}
.featuredSub {
   font-size: 15px;
   color: gray;
}

.home {
   flex: 4 1;
}
.homeWidgets {
