}

.widgetLgTh {
   text-align: left;
}

.widgetLgUser {
   display: flex;
   align-items: center;
   font-weight: 600;
}

.widgetLgImg {
   width: 40px;
   height: 40px;
   object-fit: cover;
   border-radius: 50%;
   margin-right: 10px;
}

