.widgetLg {
   flex: 2;
   -webkit-box-shadow: 5px 5px 15px -2px #000000;
   box-shadow: 5px 5px 15px -2px #000000;
   padding: 20px;
}

.widgetLgTitle {
   font-size: 20px;
   font-weight: 600;
}

.widgetLgTable {
   width: 100%;
   border-spacing: 20px;
}

.widgetLgTh {
   text-align: left;
}

.widgetLgUser {
   display: flex;
   align-items: center;
   font-weight: 600;
}

.widgetLgImg {
   width: 40px;
   height: 40px;
   object-fit: cover;
   border-radius: 50%;
   margin-right: 10px;
}

.widgetLgDate,
.widgetLgAmount {
   font-weight: 300;
}

.widgetLgButton {
   border: none;
   padding: 5px 7px;
   border-radius: 10px;
   cursor: pointer;
}

.widgetLgButton.Approved {
   background-color: #e5faf2;
   color: #3bb077;
}
.widgetLgButton.Declined {
   background-color: #fff0f1;
   color: #d95087;
}
.widgetLgButton.Pending {
   background-color: #ebf1fe;
   color: #2a7ade;
}

.widgetLgUser {
   display: flex;
   align-items: center;
   font-weight: 600;
}
